@use '../../abstracts' as *;
@use '../../base' as *;
// @use '../anchors' as *;



.slider__controls__prev,
.slider__controls__next {
    background-color: transparent;
    border-color: $color-purewhite;
    border-style: $border-style-solid;
    border-width: $border-width-hairline;
    border-radius: 50%;
    color: $color-purewhite;
    padding: $measure--threequarters;
    transition: background-color $transition-duration ease-in,
        border-color $transition-duration ease-in,
        color $transition-duration ease-in;


        @include media('reducemotion') {
            /* We can't completely disable the transition becasuse we action on transitionend */
            transition: background-color $transition-duration--instant
                    ease-in,
                border-color $transition-duration--instant ease-in,
                color $transition-duration--instant ease-in;
        }

        &:focus:not(:disabled),
        &:active:not(:disabled) {
            @include outline-properties($color-red50);
        }

        &:hover:not(:disabled) {
            @include media('hover') {
                background-color: $color-purewhite;
                color: $color-black;
            }
        }

        &:disabled {
            border-color: $color-gray7;
            border-style: $border-style-solid;
            border-width: $border-width-hairline;
            color: $color-gray7;
            cursor: default;
        }
}

.slider__controls {
    align-items: center;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    margin-top: $measure--basic;
    margin-bottom: $measure--basic;
    padding: $border-width-thick;

    .slider__indicator {
        @include smalltype();
        display: flex;
        align-items: center;
        flex-direction: row;
        color: $color-purewhite;

        .slider__indicator__current {
            font-weight: $font-weight-bold;
            &:before {
                content : "0";
            }
        }

        .slider__indicator__divider {
            margin-left: $measure--quarter;
            margin-right: $measure--quarter;

            .slider__indicator__divider--sr {
                @include screenreader-only();
            }
        }

        .slider__indicator__total {
            &:before {
                content : "0";
            }
        }

        
    }

    .slider__indicator-line {
        width: $measure--medium11 * 3;
        display: inline-block;
        border-top: $border-width-default $border-style-solid $color-red50;
        margin: $measure--half;

        @include media('<=tablet') {
            flex: 1 1 auto;
        }
        
    }

    .slider__controls__prev,
    .slider__controls__next {
        @include font-family('sharp');

        font-weight: $font-weight-normal;

        span {
            @include screenreader-only();
        }
    }

    .slider__controls__prev {
        margin-left: $measure--basic;

        &:before {
            content: '\f060';
        }

        & + .slider__controls__next {
            margin-left: $measure--basic;
        }
    }

    .slider__controls__next {
        &:before {
            content: '\f061';
            font-weight: $font-weight-normal;
        }
    }
}
