@use "../abstracts" as *;
@use "../base" as *;

main {
  @include media(">=tablet", "<small") {
    > .membership {
      @include standard-grid();
      grid-column: span 24;

      .banner {
        grid-column: span 24;
      }

      .content {
        grid-column: 1 / span -1;
      }
    }
  }

  .membership {
    .content {
      overflow: hidden;
    }
  }
}

// Desktop-specific layout
main {
  @include media(">=small") {
    > .membership {
      @include standard-grid();

      grid-column: span 24;

      .banner {
        grid-column: span 24;
      }

      .content {
        grid-column: 1 / span -1;
      }
    }
  }

  @include media(">=desktop") {
    > .membership {
      .content {
        grid-column: 4 / span 18;
      }
    }
  }
}

// Generic styles
.membership {
  padding-bottom: $measure--small2;

  .content {
    @include standard-box();
    @include media(">=tablet") {
      margin-left: 0;
      margin-right: 0;
    }
    @include media(">=1080p") {
      margin-left: auto;
      margin-right: auto;
    }
  }
}
