@use '../abstracts' as *;
@use '../base' as *;
@use '../patterns/anchors' as *;

main > .news {
    grid-column: 1 / -1;

    @include media('<small') {
        grid-column: 1 / -1 !important;

        .banner-news__label {
            @include x-column-grid(22);

            h1,
            .labels {
                grid-column: 3 / 21;
            }
        }

        .news__summary,
        .news__wrap,
        .news__body {
            grid-column: 4 / 22;
        }
    }

    @include media('<tablet') {
        .banner-news__label {
            @include x-column-grid(24);

            h1,
            .labels {
                grid-column: 1/-1;
            }
        }

        .news__summary,
        .news__wrap,
        .news__body {
            grid-column: 3 / 23;
            overflow: hidden;

            @include media('<=mobile') {
                grid-column: 1 / 24;
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .news {
            grid-column: 1 / -1 !important;

            .banner-news__label{
                @include x-column-grid(20);
            }

            .news__summary,
            .news__wrap,
            .news__body{
                grid-column: 7 / 19;
            }
        }
    }
}

