// Generic styles
main > .news {
    .content {
        padding: 0;
        margin: 0;
    }

    .banner-news__label {
        display: grid;

        h1,
        .labels {
            grid-column: 1/-1;
        }
    }

    .news__container {
        @include standard-grid();
        @include media('<mobile') {
            @include default-margin();
        }

        > div:last-child {
            border-bottom: none;
        }
    }

    .news__summary {
        padding-bottom: $measure--small2;

        p {
            @include standfirst();

        }
    }

    .news__wrap {
        border-top: $border-width-hairline $border-style-solid $color-gray4;
        padding-bottom: $measure--threequarters;
        border-bottom: $border-width-hairline $border-style-solid $color-gray4;
    }

    .news__published {
        p {
            @include smalltype();

            color: $color-text--sub;
            max-width: unset;
        }
    }

    .news__body {
        blockquote {
            p {
                @include font-family('heading');
                @include gammatype();
                @include heading-font-features();

                font-weight: $font-weight-light;
                line-height: $line-height-ratio--body;
            }
        }

        .embedded-video {
            margin-top: $measure--small2;
            padding: 0;
        }

    }

    .news__social-icons {
        margin: $measure--basic 0;

        @include media('>=tablet') {
            margin: $measure--small4 0;
        }

        p {
            font-weight: $font-weight-medium;
            margin-right: $measure--basic;
        }

        ul {
            margin-left: 0;
            margin-right: 0;
            display: flex;
            flex-direction: row;
        }

        li {
            list-style: none;
            margin-right: $measure--half;
        }
    }

}

.social-icons {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style-type: none;
    margin-bottom: $measure--small2;
    margin-left: 0;
    margin-right: 0;
    margin-top: $measure--small2;
    max-width: none;

    li {
        margin-right: $measure--threequarters;
        margin-top: 0;

        &.copyLink {
            margin-right: 0;
        }
    }
}

.social-icon {
    @include anchor-reset();
    @include zoom-in-icon();
}


///banner
/// 



// Desktop-specific layout
main > article {
    .banner-news {
        .banner__image {
            grid-column: 1 / span 24;
        }

        .banner-news__label {
            grid-column: 3 / 23;
        }
    }
}

// Generic styles
.banner-news {
    @include x-column-grid(24);
    display: grid;

    .banner__image {
        overflow: hidden;
        max-height: 400px;
    }

    .banner__block-color {
        background-color: $color-black;
        grid-column: 1 / -1;
        height: $measure--huge1;

        @include media('<tablet') {
            height: $measure--large1;
        }
    }

    .banner-news__label {
        background: $color-black;
        margin-top: 0;
        grid-column: 1 / -1;
        text-align: center;
        padding: 2rem;

        @include media('>=tablet') {
            margin-top: -$measure--medium9;
            grid-column: 2 / 24;
        }

        @include media('>small') {
            grid-column: 2 / 18;
        }

        @include media('>desktop') {
            grid-column: 3 / 17;
        }
    }


.banner__image {
    height: 100%;
    // always on first row
    grid-row: 1;

    @include media('<=tablet') {
        max-height: $banner-mobile-image-max-height;
    }

    @include media('>tablet') {
        max-height: $banner-tablet-image-max-height;
    }

    @include media('>=desktop') {
        max-height: $banner-desktop-image-max-height;
    }

    @include media('>=4K') {
        max-height: $banner-4k-image-max-height;
    }

    @include media('>=8K') {
        max-height: $banner-8k-image-max-height;
    }

    .hero-image {
        width: 100%;
        overflow: hidden;
        display: flex;

        img {
            width: 100%;
            object-fit: cover;
            align-self: auto;
        }
    }
}

    .labels {
        @include smalltype();

        color: $color-text--sub;
        font-weight: $font-weight-bold;
        margin-bottom: $measure--half;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }
}
