@use "../../abstracts" as *;
@use "../../base" as *;
@use "../anchors" as *;

// .pathway
// ul .pathway__cards  -> .pathway__list
// li .card--pathway -> pathway__list__card
// a
// .card--pathway__content -> card__content
//     .card--pathway__content__img -> card__content__img
//     .card--pathway__content__text -> card__content__txt
//     h3 .card--pathway__content__heading ->
//     span .card--pathway__content__heading__text -> p

.pathway {
  margin-top: $measure--small2;
  padding-top: $measure--basic;

  .pathway__heading {
    margin-top: 0;
    padding-left: $measure--half;
    padding-right: $measure--half;
  }

  p {
    @include smalltype();
  }

  .wrap--icon {
    margin-top: $measure--basic;

    & + h3,
    & + p {
        margin-top: $measure--quarter;
    }

    .wrap--icon__img {
        height: $measure--medium1;
        @include zetatype();
        max-width: $measure--medium1; // 2.5rem
        height: $measure--medium1;

    }
}

}

.pathway__list {
  @include media(">tablet") {
    display: grid;
    grid-column: 2/-2;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: $measure--small2;
  }

  list-style: none;
  margin: 0;
  max-width: unset;
  padding-bottom: $measure--small1;
  padding-left: $measure--half;
  padding-right: $measure--half;
}

.pathway__list__card {
  margin-top: $measure--basic;

  a {
    @include pathway-card();
  }

  .card__content {
    overflow: hidden;

    img {
      display: block;
      width: 100%;
    }

    @include media(">mobile", "<=tablet") {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;

      picture {
        max-width: $measure--huge5;
      }
    }
  }

  .card__content__text {
    padding-bottom: $measure--small3;
    padding-left: $measure--small3;
    padding-right: $measure--small3;
  }

  h3 {
    text-decoration: underline solid white;
  }
}
