////
/// Accordion
/// @group patterns
////

@use '../../abstracts' as *;
@use '../../base' as *;
@use '../anchors' as *;

@mixin accordion() {
    border-bottom: $border-width-hairline $border-style-solid $color-gray7;
    margin-top: 0;

    // Heading focus/active needed for Firefox and Safari. Other interaction states on button
    &:focus {
        outline: $border-width-default $border-style-solid $color-purewhite;
        border: 0;
    }
    &:active {
        border: 0;
        border-bottom: $border-width-hairline $border-style-solid transparent;
        outline: 0;
    }

    button {
        @include betatype();
        width: 100%;
        text-align: left;
        padding: $measure--basic;
        color: $color-purewhite;
        outline: $border-width-default $border-style-solid transparent;

        div {
            @include ninetyten-grid();
            grid-column-gap: 0;
        }

        .accordion__heading__indicator {
            align-self: center;
            text-align: right;
            height: 100%;
            color: $color-purewhite;
            

            &:before {
                display: inline-block;
                text-decoration: none;
                border: $border-width-default $border-style-solid $color-purewhite;
                background: $color-black;
                padding: $measure--quarter;
                border-radius: 50%;
                transition-property: transform;
                transition-duration: $transition-duration;
            }
        }

        &:hover {
            @include media('hover') {
                text-decoration: underline;
                color: $color-purewhite;
                outline: $border-width-default $border-style-solid transparent;
            }
        }
        &:focus {
            outline: $border-width-default $border-style-solid $color-purewhite;
            border: 0;
        }
        &:active {
            outline: $border-width-default $border-style-solid $color-purewhite;
        }
    }

    // Set top margin to give space to preceding content and stop 'jump' effect when first button is clicked
    &:first-of-type {
        margin-top: $measure--basic;
    }

    &[data-open='true'] {
        margin-top: $measure--basic;
        border-bottom: 0;

        &:focus,
        &:active {
            outline: 0;
        }

        button {
            border-top: $border-width-default $border-style-solid $color-purewhite;
            border-left: $border-width-default $border-style-solid
                $color-purewhite;
            border-right: $border-width-default $border-style-solid
                $color-purewhite;
            border-bottom: $border-width-hairline $border-style-solid
                transparent;
            color: $color-purewhite;
            padding: 0;

            .accordion__heading__text,
            .accordion__heading__indicator {
                padding: $measure--basic;
            }

            .accordion__heading__text {
                &:hover {
                    @include media('hover') {
                        text-decoration: underline;
                    }
                }
            }

            .accordion__heading__indicator {
                padding: $measure--basic $measure--basic 0;

                &:before {
                    display: inline-block;
                    text-decoration: none;
                    transform: rotate(180deg);
                    border: $border-width-default $border-style-solid $color-red50;
                    background: $color-red50;
                }
            }

            &:focus,
            &:active {
                border-top: $border-width-default $border-style-solid
                    $color-purewhite;
                border-left: $border-width-default $border-style-solid
                    $color-purewhite;
                border-right: $border-width-default $border-style-solid
                    $color-purewhite;
                border-bottom: $border-width-hairline $border-style-solid
                    $color-gray3;
                outline: 0;
            }
        }

        & + .accordion__body {
            border-top: 0;
            border-left: $border-width-default $border-style-solid
                $color-purewhite;
            border-right: $border-width-default $border-style-solid
                $color-purewhite;
            border-bottom: $border-width-default $border-style-solid
                $color-purewhite;
            padding-top: 0;

            p {
                margin: $measure--basic 0;
            }

            &:focus,
            &:active {
                /// body with border on focus only
                border-style: $border-style-solid;
                border-color: $color-purewhite;
            }

            & > :first-child {
                margin-top: 0;
            }

            & > :last-child {
                margin-bottom: 0;
            }
        }

        & + .accordion__body:first-child {
            margin-top: 0;
        }
    }
}

.js .accordion {
    margin-top: $measure--small4;
    
    @include media('screen') {
        & > .accordion__body {
            padding: $measure--basic;
        }

        & > .accordion__heading {
            @include accordion();

            &[data-open='true'] {
                & + .accordion__body {
                    margin-bottom: $measure--basic;
                }
            }

            & + .accordion__body {
                overflow-y: hidden;
                transition-duration: $transition-duration;
                transition-property: height;
                transition-timing-function: $transition-function;

                &[data-closed='true'] {
                    display: none;
                }
            }
        }
    }
}