@use "../abstracts" as *;
@use "../base" as *;

main {
    @include media(">=tablet", "<small") {
      > .news-landing {
        @include standard-grid();
        grid-column: span 24;
  
        .banner {
          grid-column: span 24;
        }
  
        .content {
          grid-column: 4 / span 18;
        }
      }
    }
  
    .news-landing {
      .content {
        overflow: hidden;
      }
    }
  }
  


// Desktop-specific layout
main {
  @include media(">=small") {
    > .news-landing {
      @include standard-grid();

      grid-column: span 24;

      .banner {
        grid-column: span 24;
      }

      .content {
        grid-column:  2 / span 22;
      }
    }
  }

  @include media(">=desktop") {
    > .news-landing {
      .content {
        grid-column: 4 / span 18;
      }
    }
  }
}

// Generic styles
.news-landing {
  padding-bottom: $measure--small2;

  .content {
    @include standard-box();
    @include media(">=tablet") {
      margin: 0;
    }
  }
}
