@use '../../abstracts' as *;
@use '../../base' as *;
@use '../../layout' as *;

.banner--image {
    margin-top: $measure--basic;
    overflow: hidden;
    position: relative;

    .banner__image {
        position: absolute;
        width: 100%;
        left: 0;
        top: 0;
        bottom: 0;

        img {
            position: relative;
            display: block;
            width: 100%;
            object-fit: cover;
            height: 100%;
        }
    }
    
    .banner__text {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: $measure--large4;
        margin-bottom: $measure--large4;
        position: relative;
        z-index: z-index('above');
    }

    .banner__heading {
        width: fit-content; 
    }

    .standfirst {
        text-align: center;
    }
}
