@use "../../abstracts" as *;
@use "../../base" as *;

.tiles__wrap {
  margin: $measure--medium1 0;
}

.tile {
  overflow: hidden;
  border-radius: $measure--half;
  text-align: center;
  color: $color-purewhite;
  background-color: $color-grey-bg;
  line-height: $line-height-ratio--body;
}

.tile__heading {
  background-color: $color-sport-red;
  color: $color-purewhite;
  font-weight: $font-weight-medium;  //600
  padding: $measure--basic;
  @include h3style();
  margin: 0;

  &--alt {
    color: $color-sport-red;
    background-color: $color-purewhite;
  }
}

.tile__details {
  padding: $measure--small1;
}

.tile__text {
  text-align: left;
  margin: 0 $measure--small1;
  padding: $measure--small1 0;
  border-top: $border-width-hairline $border-style-solid $color-ford-grey;
}
