@use 'variables' as *;
@use 'colors' as *;
@use 'measurements' as *;
@use 'breakpoints' as *;
@use 'anchor-mixins' as *;
@use 'palette' as *;
// @use '../patterns/anchors/anchors ' as *;

@mixin screenreader-only {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

@mixin veil() {
    background-color: rgba(30, 30, 30, 0.3);
    bottom: 0;
    display: none;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: z-index('veil');

    &[data-on='true'] {
        display: block;
    }

    @keyframes veilFadeIn {
        0% {
            background-color: rgba(30, 30, 30, 0);
            visibility: visible;
        }

        100% {
            background-color: rgba(30, 30, 30, 0.3);
        }
    }

    @keyframes veilFadeOut {
        0% {
            background-color: rgba(30, 30, 30, 0.3);
            visibility: visible;
        }

        100% {
            background-color: rgba(30, 30, 30, 0);
        }
    }
}

@mixin menu-animation-slideOpen {
    animation-duration: 0.4s;
    animation-name: slideOpen;

    @keyframes slideOpen {
        0% {
            max-height: 0;
            visibility: visible;
        }

        100% {
            max-height: 100vh;
        }
    }
}

@mixin menu-animation-slideClose {
    animation-duration: 0.4s;
    animation-name: slideClose;

    @keyframes slideClose {
        0% {
            max-height: 100vh;
            visibility: visible;
        }

        100% {
            max-height: 0;
            visibility: hidden;
        }
    }
}


////
// this button mixin is used on: image-carousel
////
@mixin control-button($color1: $color-purewhite) {
    background-color: transparent;
    border-color: $color-purewhite;
    border-style: $border-style-solid;
    border-width: $border-width-hairline;
    border-radius: 50%;
    color: $color-purewhite;
    padding: $measure--threequarters;
    transition: background-color $transition-duration ease-in,
        border-color $transition-duration ease-in,
        color $transition-duration ease-in;


        @include media('reducemotion') {
            /* We can't completely disable the transition becasuse we action on transitionend */
            transition: background-color $transition-duration--instant
                    ease-in,
                border-color $transition-duration--instant ease-in,
                color $transition-duration--instant ease-in;
        }

        &:focus:not(:disabled),
        &:active:not(:disabled) {
            @include outline-properties($color-red50);
        }

        &:hover:not(:disabled) {
            @include media('hover') {
                background-color: $color-purewhite;
                color: $color-black;
            }
        }

        &:disabled {
            border-color: $color-gray7;
            border-style: $border-style-solid;
            border-width: $border-width-hairline;
            color: $color-gray7;
            cursor: default;
        }
}


