@mixin pathway-card(
  $card-background-color: $color-grey-bg,
  $card-background-hover-color: $color-grey-bg--hover,
  $section-title-color: $color-text--main,
  $section-subtext-color: $color-text--sub,
  $outline-color: $color-purewhite
) {
  @include anchor-reset();
  @include background-color-and-transition(
    $card-background-color,
    $card-background-hover-color
  );

  color: $section-title-color;
  display: block;

  h3,
  .wrap--icon__img {
    color: $section-title-color;
  }

  p {
    color: $section-subtext-color;
  }

  .card__content__img {
    transition: transform $transition-duration ease-in-out;
    overflow: hidden;
  }

  @include media("reducemotion") {
    transition: none;
  }

  &:hover {
    @include media("hover") {
      .card__content__img {
        transform: scale(1.02);
      }

      h3,
      .wrap--icon__img  {
        color:$section-title-color;
       }

      p {
        color: $section-title-color;
      }
    }
  }

  &:active,
  &:focus {
    @include outline-properties($outline-color);

    h3,
    .wrap--icon__img  {
        color:$section-title-color;
       }

    p {
        color: $section-title-color;
      }

  }

  &:visited {
    h3,
    .wrap--icon__img  {
      color: $section-title-color;
    }

    p {
      color: $section-subtext-color;
    }
  }
}

@mixin background-color-and-transition(
  $initial-background-color: transparent,
  $background-color: $color-gray1
) {
  background-color: $initial-background-color;
  transition:
    background-color $transition-duration ease-in,
    transform $transition-duration ease-in-out;

  @include media("reducemotion") {
    transition: none;
  }

  &:hover {
    @include media("hover") {
      background-color: $background-color;
    }
  }

  &:active {
    background-color: $background-color;
  }

  &:focus {
    background-color: $background-color;
  }
}
