@use "../abstracts" as *;
@use "../base" as *;

main {
  @include media(">=tablet", "<small") {
    > .home {
      @include standard-grid();
      grid-column: span 24;

      .banner {
        grid-column: span 24;
      }

      .content {
        grid-column: 1 / -1;
      }
    }
  }

//   .home {
//     .content {
//       overflow: hidden;
//     }
//   }
}

// Desktop-specific layout
main {
  @include media(">=small") {
    > .home {
      @include standard-grid();

      grid-column: span 24;

      .banner {
        grid-column: span 24;
      }

      .content {
        grid-column: 4/ span 18;
      }
    }
  }

  @include media(">=desktop") {
    > .home {
      .content {
        grid-column: 4/ span 18;
      }
    }
  }
}

// Generic styles
.home {
  padding-bottom: $measure--small2;

  .content {
    @include standard-box();
    @include media(">=tablet") {
      margin: 0;
    }
  }
}
