@use '../../abstracts' as *;
@use '../../base' as *;
@use '../../layout' as *;

.blogs__container {
   margin: $measure--small4 0;
}
.listing--blogs {
    margin-bottom:$measure--small5;
}


.card--blog {
    font-weight: $font-weight-normal;
    &.card__anchor {
        &::after {
            content: none;
        }
    }
}
.card--blog__meta {
    display: flex;
    align-items: center;
    gap: $measure--half;
    font-size: $font-size--smalltype;
}
.card--blog__meta-cat {
   
    padding:$border-width-default $measure--quarter;
    color:$color-purewhite;
    background-color:$color-red50;
}

.card--blog__meta-date {
    color:$color-simple-grey;
}


.card--blog__text {
    color:$color-simple-grey;
    padding:$measure--basic 0;
    //font-size: ;
}

.card--blog__heading {
    font-weight:$font-weight-bold;
    margin-bottom:$measure--half;
    color:$color-purewhite;
    line-height: $line-height-ratio--heading;
    .card__anchor & {
        text-decoration: underline;

    }
}

.card--blog__body {
   font-size: $font-size--smalltype;
   line-height: $line-height-ratio--heading;
}
.card--blog__est-time {
    font-size: $font-size--smalltype;
    margin-top:$measure--half;
 }