@use '../../abstracts' as *;
@use '../../base' as *;
@use '../anchors' as *;

.home__accreditations {
    // grid-column: 1 / -1;
    .container {
        @include default-margin();

        @include media('>large') {
            margin-left: auto;
            margin-right: auto;
        }  
    }

    .accreditations__heading {
        margin-bottom: inherit;
    }

    .accreditations__list {
        list-style-type: none;
        margin-left: 0;
        display: flex;
        flex-wrap: wrap;
        max-width: inherit;
        justify-content: space-evenly;
        align-items: center;
        gap: $measure--small2;
    }

    .accreditations__item {
        
        @include media('<mobile') {
            flex-basis: 45%;
            display: flex;
            justify-content: center;
        }

        .logo {
            width: $measure--huge1;
        }

        .logo--5rem {
            width: auto;
            max-height: $measure--medium11;
        }

        .logo--5rem--svg {
            max-height: $measure--medium11;
        }
    }
}

